import { AfterViewInit, Component, OnInit, ViewChild, ElementRef } from '@angular/core';
import { Router } from '@angular/router';
import { ProduitService } from './../../../../../guinee/src/app/services/produit.service';

@Component({
  selector: 'app-webcams-snapshot',
  templateUrl: './webcams-snapshot.component.html',
  styleUrls: ['./webcams-snapshot.component.css']
})
export class WebcamsSnapshotComponent implements AfterViewInit {
  WIDTH = 300;
  HEIGHT = 280;

  @ViewChild("video")
  public video: ElementRef;

  @ViewChild("canvas")
  public canvas: ElementRef;

  captures: string[] = [];
  error: any;
  isCaptured: boolean;

  fichier: File;

  constructor(
    private router: Router,
    private produitService: ProduitService
    ){}

  async ngAfterViewInit() {
    await this.setupDevices();
  }

  async setupDevices() {
    if (navigator.mediaDevices && navigator.mediaDevices.getUserMedia) {
      try {
        const stream = await navigator.mediaDevices.getUserMedia({
          video: true
        });
        if (stream) {
          this.video.nativeElement.srcObject = stream;
          this.video.nativeElement.play();
          this.error = null;
        } else {
          this.error = "You have no output video device";
        }
      } catch (e) {
        this.error = e;
      }
    }
  }

  capture() {
    this.drawImageToCanvas(this.video.nativeElement);
    this.captures.push(this.canvas.nativeElement.toDataURL("image/png"));
    this.fichier = this.changeFile(this.captures[0])
    this.chercherParFichier(this.fichier)
    this.isCaptured = true;
  }

  drawImageToCanvas(image: any) {
    this.canvas.nativeElement
      .getContext("2d")
      .drawImage(image, 0, 0, this.WIDTH, this.HEIGHT);
  }

  rechercheProduit(label:string, pourcentage: string, image: string){
    if(label != null && pourcentage != null && image != null)
       this.router.navigate(["recherche/"],{queryParams: {label:label, pourcentage: pourcentage, image: image}})
  }

  changeFile(file): File {
    let fichier = new File([file], `my_image${new Date().getMilliseconds()}.jpg`, {
      type: "image/jpg",
    });
    console.log(fichier)
    return fichier;
  }

  chercherParFichier(file: File) {
    this.produitService
        .rechercheProduit(file)
        .subscribe(
          response => {
            console.log(response)
            this.rechercheProduit(response.label, response.pourcentage, response?.fichier)
          }
        )
  }

 
}
